import React from 'react';
import image from './ImagesFile/profile.jpg';

export default function Image() {
  return (
    <div
      id="myimage"
      style={{
        maxWidth: '255px',
        margin: 'auto',
      }}
    >
      <img
        className="home-img"
        src={image}
        alt="profile image"
        style={{
          borderRadius: '50%',
          width: '100%',
          height: 'auto',
        }}
      />
      <style>
        {`
        @media only screen and (min-width: 768px) {
          #myimage {
            max-width: 300px;
          }
        }
      `}
      </style>
    </div>
  );
}
